.header {
  margin-top: .5rem;
  padding-left: 10px;
}

.header ul li {
  list-style: none;
  margin: 0;
  margin-top: 5px;
  padding: .5rem 1rem;
  border-radius: 3px;
  color: #00bc8c;
}

.header ul li.active {
  background-color: #444;
  color: #fff;
}

.header h3:hover {
  color: #00bc8c;
}
