.item-list .list-group-item {
  cursor: pointer;
}

.item-list .list-group-item:hover {
  background-color: #444;
}

.li-btn {
  display: inline-block;
  width: 10px;
  height: 10px;
  margin-right: 15px;
  background-color: #444;
  border-radius: 50%;
  transition: .1s;
}

.btn-success {
  background-color: #00bc8c;
}

.btn-error {
  background-color: #d62c1a;
}